import * as React from 'react';
import 'whatwg-fetch';

import {Camper} from './Camper';
import './App.css';

export class App extends React.Component<null, {camperArray: Object}> {
    constructor(props: any) {
        super(props);

        this.state = {
            "camperArray": []
        };
    };

    fetchArray(url: string): Promise<Object> {
        return fetch(url, {
            "method": "GET",
            "mode": "cors",
            "headers": new Headers({
                "Content-Type": "application/json"
            })
        }).then((response: any) => {
            return response.json();
        }).then((res: any) => {
            this.setState({camperArray: res});
            return res;
        });
    }

    componentDidMount(): void {
        this.fetchArray("https://fcctop100.herokuapp.com/api/fccusers/top/recent").catch((err) => {
            console.log(err);
        });
    }

    handleRecent = (): any => {
        this.fetchArray("https://fcctop100.herokuapp.com/api/fccusers/top/recent").catch((err) => {
            console.log(err);
        });
    };

    handleAllTime = (): any => {
        this.fetchArray("https://fcctop100.herokuapp.com/api/fccusers/top/allTime").catch((err) => {
            console.log(err);
        });
    };

    public render(): JSX.Element {
        return (
            <div>
                <header>
                    <h1 className="Title">Camper Leaderboard</h1>
                </header>
                <main>
                    <div>
                        <table>
                            <thead>
                            <tr>
                                <th>Camper</th>
                                <th onClick={this.handleRecent}>Last 30 Days</th>
                                <th onClick={this.handleAllTime}>All-Time</th>
                            </tr>
                            </thead>
                            <Camper camperArray={this.state.camperArray}/>
                        </table>
                    </div>
                </main>
                <footer>
                    <div className="Credits">
                        <small>powered by <img
                            src="data:image/svg+xml;base64,"
                            className="App-logo" alt="logo"/></small>
                    </div>
                </footer>
            </div>
        );
    }
}


